<template>
  <v-container>
    <h2 class="text-center my-3">Daily Report</h2>
    <div class="mt-5">
      <v-sheet max-width="400" class="mx-auto transparent">
        <v-text-field
          type="month"
          outlined
          dense
          label="Select Month"
          v-model="month"
          @change="filterByMonth"
        ></v-text-field>
        <v-row class="mb-2" justify="center">
          <v-btn class="primary mx-1" large>apply</v-btn>
          <v-btn
            class="primary mx-1 hidden-md-and-up"
            @click="charts = !charts"
            large
            >charts</v-btn
          >
        </v-row>
      </v-sheet>
    </div>
    <v-card v-if="dailydata.length > 0">
      <v-card-text>
        <v-row>
          <v-col cols="12" md="6">
            <v-simple-table>
              <thead>
                <tr>
                  <th>Date</th>
                  <th>Hours</th>
                  <th>Pages</th>
                  <th>Efficiency</th>
                  <th>Update</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(d, i) in dailydata" :key="i">
                  <td>{{ new Date(d.date).toDateString() }}</td>
                  <td>{{ d.hour }}</td>
                  <td>{{ d.page }}</td>
                  <td>{{ Number(d.efficiency).toFixed(2) }}</td>
                  <td>
                    <v-btn fab small @click="editReport(d)" dark>
                      <v-icon>mdi-pencil</v-icon>
                    </v-btn>
                  </td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-col>
          <v-col cols="12" md="6" class="hidden-sm-and-down">
            <study-chart :dailydata="dailydata"></study-chart>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <div class="text-center" v-else>
      <p class="mx-auto">No data found.</p>
    </div>
    <v-dialog max-width="600" v-model="submitted">
      <v-card class="pa-5">
        <v-card-title>
          <h2 class="mx-auto text-center">Report Details</h2>
        </v-card-title>
        <v-card-text>
          <v-simple-table>
            <tbody>
              <tr>
                <td>Date:</td>
                <td>{{ new Date().toDateString() }}</td>
              </tr>
              <tr>
                <td>Hours:</td>
                <td>
                  <v-text-field
                    label="Hours"
                    type="number"
                    v-model="dayData.hour"
                  />
                </td>
              </tr>
              <tr>
                <td>Pages:</td>
                <td>
                  <v-text-field
                    placeholder="Pages"
                    type="number"
                    v-model="dayData.page"
                  />
                </td>
              </tr>
            </tbody>
          </v-simple-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn fab dark class="error" @click="submitted = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-btn fab dark @click="addDate()" :loading="loading">
            <v-icon>mdi-check</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="charts" max-width="600">
      <v-card>
        <v-card-text>
          <study-chart :dailydata="dailydata"></study-chart>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import StudyChart from "@/components/StudyChart";
export default {
  components: { StudyChart },
  data() {
    return {
      charts: false,
      month: "",
      dailydata: [],
      dayData: {
        hour: 0,
        page: 0,
      },
      submitted: false,
      loading: false,
      alldays: [],
    };
  },
  methods: {
    filterByMonth() {
      this.dailydata = this.alldays.filter(
        (d) => new Date(d.date).getMonth() == new Date(this.month).getMonth()
      );
    },
    editReport(d) {
      this.dayData = d;
      this.submitted = true;
    },
    getData() {
      fetch(
        `https://script.google.com/macros/s/AKfycbw_kdcD4HYnkByFmpIN-zUYWLeQmoqMiXcAIPc_IVtVs4h0p3NN77U18jFtwKB8TWMknw/exec?sheet=${this.user.photoURL}&type=dailydata`
      )
        .then((res) => res.json())
        .then((data) => {
          let dp = data.routines
            .filter((r) => r.date)
            .sort((a, b) => b.id - a.id);
          this.dailydata = dp.filter(
            (d) => new Date(d.date).getMonth() == new Date().getMonth()
          );
          this.alldays = dp;
        });
    },
    addDate() {
      this.loading = true;
      fetch(
        "https://script.google.com/macros/s/AKfycbw_kdcD4HYnkByFmpIN-zUYWLeQmoqMiXcAIPc_IVtVs4h0p3NN77U18jFtwKB8TWMknw/exec",
        {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            sheetid: this.user.photoURL,
            ...this.dayData,
          }),
        }
      ).then(() => {
        let index = this.dailydata.findIndex((s) => s.id == this.dayData.id);

        this.dailydata[index] = {
          ...this.dayData,
          efficiency: this.dayData.page / this.dayData.hour,
        };
        this.loading = false;
        this.submitted = false;
      });
    },
  },
  computed: {
    user() {
      return this.$store.getters.getUser;
    },
  },
  created() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
.any {
  color: rgb(9, 145, 199);
}
</style>